          ></el-table-column>
          <el-table-column prop="ratio" label="比例分成"> </el-table-column>
          <el-table-column prop="orderTotalMoney" label="收入（元）">
          </el-table-column>
          <el-table-column prop="orderCount" label="笔数"> </el-table-column>
