<script>
import moment from 'moment'
import {
  PartnerSearchList,
  PartnerCollect,
  OrderAmount,
  TotalBill,
  OrderCount
} from '@/api/account'
export default {
  data () {
    return {
      formInline: {
        user: '',
        region: ''
      },
      list: [],
      value: [
        moment().startOf('month').toDate(),
        moment().endOf('month').toDate()
      ],
      daysell: {
        OrderAmount: 0,
        TotalBill: 0,
        OrderCount: 0
      },
      monthsell: { OrderAmount: 0, TotalBill: 0, OrderCount: 0 },
      dayOfStart: moment(moment().startOf('day').toDate()).format(
        'YYYY-MM-DD HH:mm:ss'
      ),
      dayOfEnd: moment(moment().endOf('day').toDate()).format(
        'YYYY-MM-DD HH:mm:ss'
      ),
      MonthStart: moment(moment().startOf('month').toDate()).format(
        'YYYY-MM-DD HH:mm:ss'
      ),
      MonthEnd: moment(moment().endOf('month').toDate()).format(
        'YYYY-MM-DD HH:mm:ss'
      ),
      PartnerCollectList: []
    }
  },
  methods: {
    onSubmit () {
      this.PartnerCollectFn()
    },
    async PartnerSearchList () {
      const res = await PartnerSearchList()
      this.list = res.currentPageRecords
    },
    async daysellFn (start, end) {
      this.daysell.OrderAmount = await OrderAmount(start, end)
      this.daysell.TotalBill = await TotalBill(start, end)
      this.daysell.OrderCount = await OrderCount(start, end)
    },
    async monthellFn (start, end) {
      this.monthsell.OrderAmount = await OrderAmount(start, end)
      this.monthsell.TotalBill = await TotalBill(start, end)
      this.monthsell.OrderCount = await OrderCount(start, end)
    },
    async PartnerCollectFn (partnerName) {
      const res = await PartnerCollect({
        start: moment(this.value[0]).format('YYYY-MM-DD'),
        end: moment(this.value[1]).format('YYYY-MM-DD'),
        pageIndex: 1,
        pageSize: 10,
        partnerName: this.formInline.region
      })
      res.currentPageRecords.forEach((item) => {
        item.ratio = item.ratio + '%'
        item.orderTotalMoney = '+ ' + item.orderTotalMoney / 100
        item.totalBill = '+ ' + item.totalBill / 100
      })
      this.PartnerCollectList = res.currentPageRecords
    }
  },
  created () {
    this.PartnerSearchList()
    this.daysellFn(this.dayOfStart, this.dayOfEnd)
    this.monthellFn(this.MonthStart, this.MonthEnd)
    this.PartnerCollectFn()
  }
}
</script>
