import request from '@/utils/request'

// 获取一定日期范围之内的合作商分成汇总数据
export const PartnerCollect = (data) =>
  request.get('/api/order-service/report/partnerCollect', {
    params: { ...data }
  })
// 合作商搜索列表
export const PartnerSearchList = (data) =>
  request.get('/api/user-service/partner/search', {
    params: {
      pageIndex: 1,
      pageSize: 100000
    }
  })
// 获取一定时间范围之内的收入
export const OrderAmount = (start, end) =>
  request.get('/api/order-service/report/orderAmount', {
    params: {
      start: start,
      end: end
    }
  })
// 获取一定时间范围之内的分成
export const TotalBill = (start, end) =>
  request.get('/api/order-service/report/totalBill', {
    params: {
      start: start,
      end: end
    }
  })
// 获取一定时间范围之内的总数
export const OrderCount = (start, end) =>
  request.get('/api/order-service/report/orderCount', {
    params: {
      start: start,
      end: end
    }
  })
